
/*START of Navbar External styling*/


.navbar{ /*This is for the Navbar itself*/ 
    font-family: 'Poppins', sans-serif;
    padding: 10px;
    font-weight: 900;
    color: white;
    font-size: 18px;
}

.nav-link{ /*This is for the Navbar links <li>*/ 
    padding-right: 20px;
    margin-right: 25px;
    font-weight: bold;
    color: black;
    font-size: 18px;
}

.nav-link:hover{ /*This is for the Navbar links' animation */ 
    transform: scale(1.30);
    font-weight: bold;
    color: black;
}


.foooter{ /*this is for the footer*/
    background-color: none;
    color: white;
    font-style: italic;
    font-size: 12px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
}

/*END of Navbar External Styling*/

body{
    background-image: url("../img/bg.jpg");
}

/*Start of Homepage External Styling*/

.jumbotron{ /*this is for the jumbotron*/
    border: 2px solid white;
    padding: 25px;
    border-radius: 25px;
    box-shadow: 10px 8px 10px #ECECEC;
    margin-top: 175px;
    background-color: #EEEEEE;
    margin-bottom: 230px;
    animation: bounce-in-right 3s ease ;
}

@keyframes bounce-in-right {
    0% {
      opacity: 0;
      transform: translateX(2000px);
    }
    60% {
      opacity: 1;
      transform: translateX(-30px);
    }
    80% { transform: translateX(10px); }
    100% { transform: translateX(0); }
  }

.quote{
    font-style: italic;
}


.lead{
    font-family: 'Nunito', sans-serif;
    font-style: italic;
    font-weight: normal;
    color: black;
}

/*END of Homepage External Styling*/



/*Start of Projects External Styling*/

.card{
    width: 70%;
    height: 65%;
    margin: auto;
    border-radius: 20px;
    padding: 15px;
    box-shadow: 8px 6px 8px #252525;
}

.card-demo{
    box-shadow: 3px 6px 4px 4px #888888;
}

/*End of Projects External Styling*/


/*Start of About Me External Styling*/
.img-about{
    border-radius: 25px;
    box-shadow: 7px 7px 7px 7px black;
    padding-left: 7%
}

.about-right{
    padding-left: 7%;
}

.about-title{
    font-family: 'Kolker Brush', cursive;
    font-size: 80px;
/*
font-family: 'Nunito', sans-serif;
*/
    font-weight: bold;
}

.about-paragraph{
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    font-weight: bold;
    color: black;
    text-align: justify;
    background-color: #DFDFDE;
    padding: 25px;
    border-radius: 25px;
    margin-bottom: 50px;
}

.img-prog{
    width: 20%;
    height: 20%;
    padding-left: 15px;
    animation: gelatine 2s infinite;
}

@keyframes gelatine {
    from, to { transform: scale(1, 1); }
    25% { transform: scale(0.9, 1.1); }
    50% { transform: scale(1.1, 0.9); }
    75% { transform: scale(0.95, 1.05); }
  }


.containerr{
    position: relative;
    width: 90%;
    height:100%;
}
.img-about{
  display: block;
  width: 100%;
  height: 100%;
}
.containerr:hover .overlay {
    bottom: 0;
    height: 100%;
  }
.overlay {
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    background-color: #F7F5F2;
    overflow: hidden;
    width: 100%;
    height:0;
    transition: .5s ease;
    border-radius: 25px;
  }
  .text {
    color: black;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
  }


  .about-proglang{
    color: white;
  }

  button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 25px;
  }



/*End of About Me External Styling*/

